.btn {
  display: inline-block;
  line-height: (20/16);
  font-weight: 400;
  cursor: pointer;
  padding: (18em/16) (30em/16);
  text-align: center;
  min-width: (100em/16);
}

.btn--full {
  color: $color-text;
  background: $color-accent;
  transition: box-shadow $transition;

  &:hover {
    color: $color-text; 
    box-shadow: inset 0px 4px 4px rgba(0, 63, 109, 0.6);
  }
}

.btn--line {
  color: $color-text;
  background-color: transparent;
  box-shadow: inset 0 0 0 1px #fff;
  transition: box-shadow $transition, background-color $transition;
  &:hover {
    color: $color-text; 
    box-shadow: inset 0 0 0 1px transparent;
    background: $color-accent;
  }
}

.btn--big-min-width {
  min-width: (270em/16);
}