$color-black: #050505;
$color-white: #fff;
$color-blue: #00B9D2;
$color-gray: #666666;

$color-background: $color-black;
$color-accent: $color-blue;

$color-text: $color-white;
$color-text-accent: $color-accent;
$color-text-gray: $color-gray;

$font-family: 'RF Dewi', Arial, Helvetica, sans-serif;
$font-family-exta: 'RF Dewi Expanded', $font-family;

$transition: 0.2s ease-out;