@mixin font-size-text() {
  font-size: 1rem;
  @include media-breakpoint-up('sm') {
    font-size: 1rem;
  }
}
@mixin css-text() {
  line-height: 1.7;
  font-family: $font-family;
}
@mixin all-text() {
  @include css-text;
  @include font-size-text;
}

@mixin font-size-h1() {
  font-size: (32rem/16);
  line-height: (58/48);
  @include media-breakpoint-up('sm') {
    font-size: (36rem/16);
  }

  @include media-breakpoint-up('md') {
    font-size: (40rem/16);
  }

  @include media-breakpoint-up('lg') {
    font-size: (44rem/16);
  }

  @include media-breakpoint-up('xl') {
    font-size: (48rem/16);
  }
}
@mixin css-h1() {
  font-family: $font-family-exta;
  font-weight: 400;
}
@mixin all-h1() {
  @include css-h1;
  @include font-size-h1;
}

@mixin font-size-h2() {
  font-size: (24rem/16);
  line-height: (38/32);
  @include media-breakpoint-up('md') {
    font-size: (28rem/16);
  }
  @include media-breakpoint-up('xl') {
    font-size: (32rem/16);
  }
}
@mixin css-h2() {
  font-family: $font-family-exta;
  text-transform: uppercase;
  font-weight: 400;
}
@mixin all-h2() {
  @include css-h2;
  @include font-size-h2;
}

@mixin font-size-h3() {
  font-size: (20rem/16);
  line-height: (26/22);

  @include media-breakpoint-up('md') {
    font-size: (22rem/16);
  }
}
@mixin css-h3() {
  font-family: $font-family-exta;
  font-weight: 400;
}
@mixin all-h3() {
  @include css-h3;
  @include font-size-h3;
}

@mixin font-size-h4() {
  font-size: (18rem/16);
  line-height: 1.5;
  @include media-breakpoint-up('lg') {
    font-size: (20rem/16);
  } 
}
@mixin css-h4() {
  font-weight: 700;
}
@mixin all-h4() {
  @include css-h4;
  @include font-size-h4;
}