.label {
  display: block;
  margin-top: (60em/20);
  margin-bottom: 1.3em;
}

.input {
  display: block;
  width: 100%;
  color: $color-text;
  border: 1px solid #666666;
  padding: 0 (18em/16);
  font-size: inherit;
  position: relative;
  min-height: (56em/16);

  input, textarea {
    color: inherit;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border: 0;
  }
  input {
    padding: 0 (18em/16);
  }
  textarea {
    padding: (12em/16) (18em/16);
  }
}
input.input {
  height: (56em/16);
}

textarea.input {
  padding-top: (12em/16);
  padding-bottom: (12em/16);
  height: (56em/16);
}

textarea.input, .input textarea {
  resize: none;
}

.input + .label {
  margin-top: 3em;
}

.radio-input {
  &__label {
    display: flex;
    position: relative;
    align-items: center;
    margin-bottom: (30rem/16);
    &:last-child {
      margin-bottom: 0;
    }
    &::before {
      content: '';
      width: 1em;
      height: 1em;
      border: 1px solid #FFFFFF;
      margin-right: 0.5em;
      border-radius: 50%;
    }

    input {
      font-size: inherit;
      position: absolute;
      top: 50%;
      left: 0.5em;
      width: (6em/16);
      height: (6em/16);
      min-width: 6px;
      min-height: 6px;
      border-radius: 50%;
      transform: translate(-50%, -50%);
      &:checked {
        background: $color-accent;
      }
    }

    &--line {
      display: inline-flex;
      margin-right: (48rem/16);
      margin-bottom: 0.5em;
      &:last-child {
        margin-right: 0;
      }
    }
  }
}

.checkbox-input {
  &__label {
    display: flex;
    position: relative;
    align-items: center;
    margin-bottom: (30rem/16);
    &:last-child {
      margin-bottom: 0;
    }
    &::before {
      content: '';
      width: 1em;
      height: 1em;
      border: 1px solid #FFFFFF;
      margin-right: 0.5em;
    }

    input {
      font-size: inherit;
      position: absolute;
      top: 50%;
      left: 0.5em;
      width: 1em;
      height: 1em;
      transform: translate(-50%, -50%);
      &:checked {
        background: url('../img/check.svg') no-repeat center center $color-accent;
        background-size: contain;
      }
    }

    &--line {
      display: inline-flex;
      margin-right: (48rem/16);
      margin-bottom: 0.5em;
      &:last-child {
        margin-right: 0;
      }
    }
  }
}