.courser {
  width: (70400vw/1920);
  height: (26200vh/1080);
  background: $color-accent;
  filter: blur(200px);
  opacity: 0;
  position: absolute;
  left: 0;
  top: 0;
  border-radius: 50%;
  transition: opacity $transition, transform 0.1s;
  &__wrapper {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: -1;
  }
}

.courser__wrapper.visible .courser{
  opacity: 0.4;
}